@import "datatables-core";
@import "datatables-autofill";
@import "datatables-buttons";
@import "datatables-responsive";
@import "datatables-select";

div.dataTables_wrapper div.dataTables_filter {
  text-align: left;
}

div.dataTables_wrapper div.dataTables_filter input {
    margin-left: 0;
    margin-right: 15px;
}

div.dataTables_wrapper div.dataTables_info {
  padding-top: 4px;
  white-space: nowrap;
}

div.dataTables_wrapper div.dataTables_processing {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 200px;
  margin-left: -100px;
  text-align: center;
}

#margin-top-botton-in-dt {
  padding-top: 10px;
  margin-top: 14px;
  margin-bottom: 14px;
}

table.dataTable {
  clear: both;
}

div.dataTables_wrapper div.dataTables_paginate {
  text-align: right
}

div.dataTables_wrapper div.mdl-grid.dt-table {
  padding-top: 0;
  padding-bottom: 0
}

  div.dataTables_wrapper div.mdl-grid.dt-table > div.mdl-cell {
      margin-top: 0;
      margin-bottom: 0
  }

table.dataTable thead > tr > th.sorting_asc, table.dataTable thead > tr > th.sorting_desc, table.dataTable thead > tr > th.sorting, table.dataTable thead > tr > td.sorting_asc, table.dataTable thead > tr > td.sorting_desc, table.dataTable thead > tr > td.sorting {
  padding-right: 30px
}

table.dataTable thead > tr > th:active, table.dataTable thead > tr > td:active {
  outline: none;
}

table.dataTable thead .sorting, table.dataTable thead .sorting_asc, table.dataTable thead .sorting_desc, table.dataTable thead .sorting_asc_disabled, table.dataTable thead .sorting_desc_disabled {
  cursor: pointer;
  position: relative;
}

  table.dataTable thead .sorting:before, table.dataTable thead .sorting:after, table.dataTable thead .sorting_asc:before, table.dataTable thead .sorting_asc:after, table.dataTable thead .sorting_desc:before, table.dataTable thead .sorting_desc:after, table.dataTable thead .sorting_asc_disabled:before, table.dataTable thead .sorting_asc_disabled:after, table.dataTable thead .sorting_desc_disabled:before, table.dataTable thead .sorting_desc_disabled:after {
      position: absolute;
      bottom: 7px;
      display: block;
      opacity: 0.3;
      font-size: 1em;
  }

  table.dataTable thead .sorting:before, table.dataTable thead .sorting_asc:before, table.dataTable thead .sorting_desc:before, table.dataTable thead .sorting_asc_disabled:before, table.dataTable thead .sorting_desc_disabled:before {
      right: 1em;
      content: "\2191";
  }

  table.dataTable thead .sorting:after, table.dataTable thead .sorting_asc:after, table.dataTable thead .sorting_desc:after, table.dataTable thead .sorting_asc_disabled:after, table.dataTable thead .sorting_desc_disabled:after {
      right: 0.5em;
      content: "\2193";
  }

  table.dataTable thead .sorting_asc:before, table.dataTable thead .sorting_desc:after {
      opacity: 1;
  }

  table.dataTable thead .sorting_asc_disabled:before, table.dataTable thead .sorting_desc_disabled:after {
      opacity: 0;
  }

  table.dataTable.dt-checkboxes-select tbody tr,
  table.dataTable thead th.dt-checkboxes-select-all,
  table.dataTable tbody td.dt-checkboxes-cell {
    cursor: pointer;
  }
  
  table.dataTable thead th.dt-checkboxes-select-all,
  table.dataTable tbody td.dt-checkboxes-cell {
    text-align: center;
  }
  
  div.dataTables_wrapper span.select-info,
  div.dataTables_wrapper span.select-item {
    margin-left: 0.5em;
  }
  
  @media screen and (max-width: 640px) {
    div.dataTables_wrapper span.select-info,
    div.dataTables_wrapper span.select-item {
      margin-left: 0;
      display: block;
    }
  }